A,gBAAA,CAIA,SAAA,CAFA,cAAA,CALA,OAAA,CAMA,iBAAA,CAPA,KAAA,CAEA,UApHR,CACF,CAmIA,yBACE,WAEE,oBAjIF,CAmIA,gBAEE,kBAjIF,CAmIA,iBAEE,qBAjIF,CAmIA,eACE,gBC/gBkC,CDghBlC,iBAjIF,CACF","sourcesContent":["/* Magnific Popup CSS */\n\n@import \"settings\";\n\n////////////////////////\n//\n// Contents:\n//\n// 1. General styles\n//    - Transluscent overlay\n//    - Containers, wrappers\n//    - Cursors\n//    - Helper classes\n// 2. Appearance\n//    - Preloader & text that displays error messages\n//    - CSS reset for buttons\n//    - Close icon\n//    - \"1 of X\" counter\n//    - Navigation (left/right) arrows\n//    - Iframe content type styles\n//    - Image content type styles\n//    - Media query where size of arrows is reduced\n//    - IE7 support\n//\n////////////////////////\n\n\n\n////////////////////////\n// 1. General styles\n////////////////////////\n\n// Transluscent overlay\n.mfp-bg {\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  z-index: $mfp-z-index-base + 2;\n  overflow: hidden;\n  position: fixed;\n\n  background: $mfp-overlay-color;\n  opacity: $mfp-overlay-opacity;\n}\n\n// Wrapper for popup\n.mfp-wrap {\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  z-index: $mfp-z-index-base + 3;\n  position: fixed;\n  outline: none !important;\n  -webkit-backface-visibility: hidden; // fixes webkit bug that can cause \"false\" scrollbar\n}\n\n// Root container\n.mfp-container {\n  text-align: center;\n  position: absolute;\n  width: 100%;\n  height: 100%;\n  left: 0;\n  top: 0;\n  padding: 0 $mfp-popup-padding-left;\n  box-sizing: border-box;\n}\n\n// Vertical centerer helper\n.mfp-container {\n  &:before {\n    content: '';\n    display: inline-block;\n    height: 100%;\n    vertical-align: middle;\n  }\n}\n\n// Remove vertical centering when popup has class `mfp-align-top`\n.mfp-align-top {\n  .mfp-container {\n    &:before {\n      display: none;\n    }\n  }\n}\n\n// Popup content holder\n.mfp-content {\n  position: relative;\n  display: inline-block;\n  vertical-align: middle;\n  margin: 0 auto;\n  text-align: left;\n  z-index: $mfp-z-index-base + 5;\n}\n.mfp-inline-holder,\n.mfp-ajax-holder {\n  .mfp-content {\n    width: 100%;\n    cursor: auto;\n  }\n}\n\n// Cursors\n.mfp-ajax-cur {\n  cursor: progress;\n}\n.mfp-zoom-out-cur {\n  &, .mfp-image-holder .mfp-close {\n    cursor: -moz-zoom-out;\n    cursor: -webkit-zoom-out;\n    cursor: zoom-out;\n  }\n}\n.mfp-zoom {\n  cursor: pointer;\n  cursor: -webkit-zoom-in;\n  cursor: -moz-zoom-in;\n  cursor: zoom-in;\n}\n.mfp-auto-cursor {\n  .mfp-content {\n    cursor: auto;\n  }\n}\n\n.mfp-close,\n.mfp-arrow,\n.mfp-preloader,\n.mfp-counter {\n  -webkit-user-select:none;\n  -moz-user-select: none;\n  user-select: none;\n}\n\n// Hide the image during the loading\n.mfp-loading {\n  &.mfp-figure {\n    display: none;\n  }\n}\n\n// Helper class that hides stuff\n@if $mfp-use-visuallyhidden {\n  // From HTML5 Boilerplate https://github.com/h5bp/html5-boilerplate/blob/v4.2.0/doc/css.md#visuallyhidden\n  .mfp-hide {\n    border: 0 !important;\n    clip: rect(0 0 0 0) !important;\n    height: 1px !important;\n    margin: -1px !important;\n    overflow: hidden !important;\n    padding: 0 !important;\n    position: absolute !important;\n    width: 1px !important;\n  }\n} @else {\n  .mfp-hide {\n    display: none !important;\n  }\n}\n\n\n////////////////////////\n// 2. Appearance\n////////////////////////\n\n// Preloader and text that displays error messages\n.mfp-preloader {\n  color: $mfp-controls-text-color;\n  position: absolute;\n  top: 50%;\n  width: auto;\n  text-align: center;\n  margin-top: -0.8em;\n  left: 8px;\n  right: 8px;\n  z-index: $mfp-z-index-base + 4;\n  a {\n    color: $mfp-controls-text-color;\n    &:hover {\n      color: $mfp-controls-text-color-hover;\n    }\n  }\n}\n\n// Hide preloader when content successfully loaded\n.mfp-s-ready {\n  .mfp-preloader {\n    display: none;\n  }\n}\n\n// Hide content when it was not loaded\n.mfp-s-error {\n  .mfp-content {\n    display: none;\n  }\n}\n\n// CSS-reset for buttons\nbutton {\n  &.mfp-close,\n  &.mfp-arrow {\n    overflow: visible;\n    cursor: pointer;\n    background: transparent;\n    border: 0;\n    -webkit-appearance: none;\n    display: block;\n    outline: none;\n    padding: 0;\n    z-index: $mfp-z-index-base + 6;\n    box-shadow: none;\n    touch-action: manipulation;\n  }\n  &::-moz-focus-inner {\n      padding: 0;\n      border: 0\n  }\n}\n\n\n// Close icon\n.mfp-close {\n  width: 44px;\n  height: 44px;\n  line-height: 44px;\n\n  position: absolute;\n  right: 0;\n  top: 0;\n  text-decoration: none;\n  text-align: center;\n  opacity: $mfp-controls-opacity;\n  padding: 0 0 18px 10px;\n  color: $mfp-controls-color;\n\n  font-style: normal;\n  font-size: 28px;\n  font-family: Arial, Baskerville, monospace;\n\n  &:hover,\n  &:focus {\n    opacity: 1;\n  }\n\n  &:active {\n    top: 1px;\n  }\n}\n.mfp-close-btn-in {\n  .mfp-close {\n    color: $mfp-inner-close-icon-color;\n  }\n}\n.mfp-image-holder,\n.mfp-iframe-holder {\n  .mfp-close {\n    color: $mfp-controls-color;\n    right: -6px;\n    text-align: right;\n    padding-right: 6px;\n    width: 100%;\n  }\n}\n\n// \"1 of X\" counter\n.mfp-counter {\n  position: absolute;\n  top: 0;\n  right: 0;\n  color: $mfp-controls-text-color;\n  font-size: 12px;\n  line-height: 18px;\n  white-space: nowrap;\n}\n\n// Navigation arrows\n@if $mfp-include-arrows {\n  .mfp-arrow {\n    position: absolute;\n    opacity: $mfp-controls-opacity;\n    margin: 0;\n    top: 50%;\n    margin-top: -55px;\n    padding: 0;\n    width: 90px;\n    height: 110px;\n    -webkit-tap-highlight-color: rgba(0,0,0,0);\n    &:active {\n      margin-top: -54px;\n    }\n    &:hover,\n    &:focus {\n      opacity: 1;\n    }\n    &:before,\n    &:after {\n      content: '';\n      display: block;\n      width: 0;\n      height: 0;\n      position: absolute;\n      left: 0;\n      top: 0;\n      margin-top: 35px;\n      margin-left: 35px;\n      border: medium inset transparent;\n    }\n\n    &:after {\n\n      border-top-width: 13px;\n      border-bottom-width: 13px;\n      top:8px;\n    }\n\n    &:before {\n      border-top-width: 21px;\n      border-bottom-width: 21px;\n      opacity: 0.7;\n    }\n\n  }\n\n  .mfp-arrow-left {\n    left: 0;\n    &:after {\n      border-right: 17px solid $mfp-controls-color;\n      margin-left: 31px;\n    }\n    &:before {\n      margin-left: 25px;\n      border-right: 27px solid $mfp-controls-border-color;\n    }\n  }\n\n  .mfp-arrow-right {\n    right: 0;\n    &:after {\n      border-left: 17px solid $mfp-controls-color;\n      margin-left: 39px\n    }\n    &:before {\n      border-left: 27px solid $mfp-controls-border-color;\n    }\n  }\n}\n\n\n\n// Iframe content type\n@if $mfp-include-iframe-type {\n  .mfp-iframe-holder {\n    padding-top: $mfp-iframe-padding-top;\n    padding-bottom: $mfp-iframe-padding-top;\n    .mfp-content {\n      line-height: 0;\n      width: 100%;\n      max-width: $mfp-iframe-max-width;\n    }\n    .mfp-close {\n      top: -40px;\n    }\n  }\n  .mfp-iframe-scaler {\n    width: 100%;\n    height: 0;\n    overflow: hidden;\n    padding-top: $mfp-iframe-ratio * 100%;\n    iframe {\n      position: absolute;\n      display: block;\n      top: 0;\n      left: 0;\n      width: 100%;\n      height: 100%;\n      box-shadow: $mfp-shadow;\n      background: $mfp-iframe-background;\n    }\n  }\n}\n\n\n\n// Image content type\n@if $mfp-include-image-type {\n\n  /* Main image in popup */\n  img {\n    &.mfp-img {\n      width: auto;\n      max-width: 100%;\n      height: auto;\n      display: block;\n      line-height: 0;\n      box-sizing: border-box;\n      padding: $mfp-image-padding-top 0 $mfp-image-padding-bottom;\n      margin: 0 auto;\n    }\n  }\n\n  /* The shadow behind the image */\n  .mfp-figure {\n    line-height: 0;\n    &:after {\n      content: '';\n      position: absolute;\n      left: 0;\n      top: $mfp-image-padding-top;\n      bottom: $mfp-image-padding-bottom;\n      display: block;\n      right: 0;\n      width: auto;\n      height: auto;\n      z-index: -1;\n      box-shadow: $mfp-shadow;\n      background: $mfp-image-background;\n    }\n    small {\n      color: $mfp-caption-subtitle-color;\n      display: block;\n      font-size: 12px;\n      line-height: 14px;\n    }\n    figure {\n      margin: 0;\n    }\n  }\n  .mfp-bottom-bar {\n    margin-top: -$mfp-image-padding-bottom + 4;\n    position: absolute;\n    top: 100%;\n    left: 0;\n    width: 100%;\n    cursor: auto;\n  }\n  .mfp-title {\n    text-align: left;\n    line-height: 18px;\n    color: $mfp-caption-title-color;\n    word-wrap: break-word;\n    padding-right: 36px; // leave some space for counter at right side\n  }\n\n  .mfp-image-holder {\n    .mfp-content {\n      max-width: 100%;\n    }\n  }\n\n  .mfp-gallery {\n    .mfp-image-holder {\n      .mfp-figure {\n        cursor: pointer;\n      }\n    }\n  }\n\n\n  @if $mfp-include-mobile-layout-for-image {\n    @media screen and (max-width: 800px) and (orientation:landscape), screen and (max-height: 300px) {\n      /**\n       * Remove all paddings around the image on small screen\n       */\n      .mfp-img-mobile {\n        .mfp-image-holder {\n          padding-left: 0;\n          padding-right: 0;\n        }\n        img {\n          &.mfp-img {\n            padding: 0;\n          }\n        }\n        .mfp-figure {\n          // The shadow behind the image\n          &:after {\n            top: 0;\n            bottom: 0;\n          }\n          small {\n            display: inline;\n            margin-left: 5px;\n          }\n        }\n        .mfp-bottom-bar {\n          background: rgba(0,0,0,0.6);\n          bottom: 0;\n          margin: 0;\n          top: auto;\n          padding: 3px 5px;\n          position: fixed;\n          box-sizing: border-box;\n          &:empty {\n            padding: 0;\n          }\n        }\n        .mfp-counter {\n          right: 5px;\n        